.header {
    background-color: $primaryColor;
    // border: 1px solid;

    // @media screen and (min-width: 768px){
    //     box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.15);
    // }

    @include media(sm) {
        box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.15);
    }

   .container {
        @extend %container;
    }
}



.header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 6rem;
    @include media(sm) {
        height: 10rem;
    }
}
.header__wrapper {
    display: flex;
    align-items: center;
}

.header__link {
    margin-right: 4.5rem;
    @include media(xs) {
        margin-right: 3.6rem;
    }
    @include media(sm) {
        height: 2.3rem;
        width: 13rem;
    }
    .logo {
        width: 100%;
        height: 100%
    }
}

.header__city-link {
    @include text(1.4rem, 400, 1.2, $linkColor);
    @include media(xs) {
        display: none;
    }
}

.header__phone {
    display: none;
    span {
        @include text(1.4rem, 400, 1.2, $textColorLight);
    }
    @include media(xs) {
        display: block;
    }
}

.header__phone-link {
    @include text(1.4rem, 400, 1.2, $textColor);
}

.header__user {
    @include media(md) {
        display: none;
    }
}