.calendar {
    padding: 2rem 0 0;
}

.calendar__title {
    @include text(2.6rem, 400, 1.2, $textColor);

    margin-bottom: 4rem;
    text-align: center;

    @include media(xs) {
        margin: 3rem auto 6rem;

        max-width: 30rem;

        // border-top: 0.5px solid $secondaryColor;
    }
}

.calendar__date {
    @include text(1.8rem, 500, 1.4, $textColor);

    display: block;

    padding: 1.8rem 0 2rem;

    position: relative;

    border-top: 0.5px solid $secondaryColor;
    text-align: center;

    &::after {
        display: none;

        width: 35px;
        height: 35px;

        position: absolute;

        content: "";
        top: 17px;
        left: 237px;
        background: url(./../images/calendar.svg) no-repeat;

        cursor: pointer;

        @include media(xs) {
            display: block;
        }

    }


    @include media(xs) {
        padding-left: 2rem;
        text-align-last: left;

    }
}

.calendar__box {
    border-top: 0.5px solid $secondaryColor;
}

.calendar__time {
    @include text(1.8rem, 400, 1.4, $textColor);

    display: block;

    padding: 2.2rem 2rem 2rem;



}

.calendar__price {
    @include text(1.8rem, 600, 1.1, $textColor);

    display: block;

    padding: 0 2rem .5rem;



}

.calendar__place {
    @include text(1.8rem, 400, 1.4, $textColor);

    display: block;

    padding: 0 2rem 2rem;

}

.calendar__btn {
    @include text(1.4rem, 500, 1.1, $accentColor);
    width: 13rem;
    height: 5rem;

    margin: 0 2rem 3rem;

    border: 2px solid $accentColor;
    border-radius: 3px;
    background-color: transparent;
    transition: all .3s linear;


    &:hover {
        border-color: lighten($accentColor, 15%);
    }

    &:active {
        border-color: darken($accentColor, 5%);
    }
}